import Rows from './Rows'

export default function SQD() {
    return (
        <div className='container py-3 mb-3 overflow-x-scroll'>
            <h5 className='mb-3'>SQD Questions</h5>
            <table className='table table-hover table-striped-columns table-success'>
                <thead>
                    <tr>
                        <th></th>
                        <th>Strongly Agree</th>
                        <th>Agree</th>
                        <th>Neither Agree Nor Disagree</th>
                        <th>Disagree</th>
                        <th>Strongly Disagree</th>
                        <th>N/A (Not Applicable)</th>
                        <th>Total Clients</th>
                        <th>Percentage</th>
                    </tr>
                </thead>
                <tbody>
                    <Rows type='sqd' />
                </tbody>
            </table>
        </div>
    )
}