import { useState, useEffect, useContext } from 'react'

import { GlobalState } from '../../contexts/globalContext'

export default function Rows({ type }) {
    const state = useContext(GlobalState)

    const [result, setResult] = useState(undefined)
    const [keys, setKeys] = useState([])

    useEffect(() => {
        const compute = () => {
            let temp = []

            if (type === 'cc') {
                for (let x = 0; x < 3; x++) {
                    temp.push({ question: `CC${x + 1}`, opt1: 0, opt2: 0, opt3: 0, opt4: 0, opt5: 0, total: 0 })
                }

                setKeys(Object.keys(temp[0]))

                state.filtered_data.forEach(row => {
                    Object.keys(row).forEach(key => {
                        for (let y = 0; y < temp.length; y++) {
                            if (key.startsWith(`CC${y + 1}`) || key.startsWith(`"CC${y + 1}`)) {
                                temp[y].total++

                                for (let x = 0; x < (Object.keys(temp[0]).length - 1); x++) {
                                    if (row[key].startsWith(`${x + 1}`) || row[key].startsWith(`"${x + 1}`)) {
                                        temp[y][`opt${x + 1}`]++
                                    }
                                }
                            }
                        }
                    })
                })

                setResult(temp)
            } else if (type === 'sqd') {
                for (let x = 0; x < 9; x++) {
                    temp.push({ question: `SQD${x}`, opt1: 0, opt2: 0, opt3: 0, opt4: 0, opt5: 0, opt6: 0, total: 0, percentage: 0 })
                }

                temp.push({ question: 'Total', opt1: 0, opt2: 0, opt3: 0, opt4: 0, opt5: 0, opt6: 0, total: 0, percentage: 0 })

                setKeys(Object.keys(temp[0]))

                state.filtered_data.forEach(row => {
                    Object.keys(row).forEach(key => {
                        for (let y = 0; y < temp.length; y++) {
                            if (key.startsWith(`SQD${y}`) || key.startsWith(`"SQD${y}`)) {
                                temp[y].total++

                                for (let x = 0; x < (Object.keys(temp[0]).length - 1); x++) {
                                    if (row[key].startsWith(`${x + 1}`) || row[key].startsWith(`"${x + 1}`)) {
                                        temp[y][`opt${x + 1}`]++
                                    }
                                }
                            }
                        }
                    })
                })

                temp.forEach((row, index) =>{
                    if (index !== 9) {
                        temp[9].opt1 = temp[9].opt1 + row.opt1
                        temp[9].opt2 = temp[9].opt2 + row.opt2
                        temp[9].opt3 = temp[9].opt3 + row.opt3
                        temp[9].opt4 = temp[9].opt4 + row.opt4
                        temp[9].opt5 = temp[9].opt5 + row.opt5
                        temp[9].opt6 = temp[9].opt6 + row.opt6
                        temp[9].total = temp[9].total + row.total
                    }
                })

                temp.forEach((row) => {
                    // index === 5 ? row.percentage = 'N/A' : 
                    row.percentage = `${(((row.opt1 + row.opt2) / (row.total - row.opt6)) * 100).toLocaleString('en-US')}%`
                })

                setResult(temp)
            } else if (type === 'services') {
                const services = [
                    'AICS',
                    'Social Case Study',
                    'Issuance of Senior Citizen ID',
                ]

                services.forEach(service => temp.push({ question: service, overallRating: 0 }))

                setKeys(Object.keys(temp[0]))

                state.filtered_data.forEach(row => {
                    Object.keys(row).forEach(key => {
                        if (key.startsWith(`Uri`) || key.startsWith(`"Uri`)) {
                            for(let x = 0; x < services.length; x++) {
                                if (row[key].startsWith(`${services[x]}}`) || row[key].startsWith(`"${services[x]}`) || row[key] === `${services[x]}` || row[key] === `"${services[x]}"`) {
                                    temp[x].overallRating++
                                }
                            }
                        }
                    })
                })

                setResult(temp)
            }
        }

        compute()

        //eslint-disable-next-line
    }, [state.filtered_data])

    return (
        <>
            {
                result !== undefined && result.map((row, index) => (
                    <tr key={index}>
                        {keys.length > 0 && keys.map((key, i) => (
                            <td key={i}>{row[key]}</td>
                        ))}
                    </tr>
                ))
            }
        </>
    )
}