export const useFetch = () => {
    const fetch_online = async () => {
        try {
            const raw = await fetch(process.env.REACT_APP_CSM_LINK)
            const response = await raw.text()
            return response
        } catch (err) {
            console.log(err)
            return err.message
        }
        
    }

    return fetch_online
}