import { v4 as uuid } from 'uuid'

const csv_to_array = (csv) => {
    if (csv) {
        const headers = csv.split('\r\n')[0].split(/,(?=(?:(?:[^"]*"){2})*[^"]*$)/)
        let result = []
        const csv_array = csv.split('\r\n')

        csv_array.forEach((row, index) => {
            let unique_id = uuid()

            if (index !== 0) {
                let obj = {}

                const current = row.split(/,(?=(?:(?:[^"]*"){2})*[^"]*$)/)

                current.forEach((data, i) => obj = { ...obj, [headers[i]]: data })

                obj = { ...obj, id: unique_id.slice(0, 16) }
                obj = { ...obj, Timestamp: new Date(obj.Timestamp) }

                result.push(obj)
            }
        })

        return { headers, result }
    }
}

const filter_array = (array, month, year) => {
    let newArray

    if (month === 'all') {
        newArray = array.filter(row => row['Petsa'].split('/')[2].toString() === year)
    } else {
        newArray = array.filter(row => row['Petsa'].split('/')[0].toString() === month.toString() && row['Petsa'].split('/')[2].toString() === year)
    }

    return newArray
}

export const globalReducer = (state, action) => {
    switch (action.type) {
        case 'start-loading': {
            return { ...state, loading: true }
        }
        case 'end-loading': {
            return { ...state, loading: false }
        }
        case 'set-data': {
            let { headers, result } = csv_to_array(action.payload)
            return { ...state, data: result, headers: headers }
        }
        case 'set-month': {
            return { ...state, month: action.payload }
        }
        case 'set-year': {
            return { ...state, year: action.payload }
        }
        case 'filter-data': {
            let newArray = filter_array(state.data, state.month, state.year.toString())
            return { ...state, filtered_data: newArray, count: newArray.length }
        }
        default: {
            throw new Error('Undefined dispatch action.')
        }
    }
}

export const initialState = {
    loading: true,
    headers: [],
    data: [],
    month: (new Date().getMonth() + 1).toString(),
    year: (new Date().getFullYear()).toString(),
    count: 0,
    filtered_data: []
}