import city from '../assets/images/city.png'
import cswdo from '../assets/images/cswdo.png'

export default function Footer() {
    return (
        <div className='container-fluid bg-body-secondary text-center py-5'>
            <div className='row justify-content-center mb-4'>
                <div className='container d-flex gap-3 justify-content-center'>
                    <img alt='city-logo' src={city} height={50} width={50} />
                    <img alt='cswdo-logo' src={cswdo} height={50} width={50} />
                </div>
            </div>
            <p className='text-secondary mb-0' style={{ fontSize: '0.85rem' }}>City of San Jose del Monte, Bulacan</p>
            <p className='text-secondary fw-bold'>City Social Welfare and Development Office</p>
            <p className='text-secondary fw-bold' style={{ fontSize: '0.75rem' }}>@2023</p>
        </div>
    )
}