import Rows from './Rows'

export default function CitizensCharter() {
    return (
        <div className='container py-3 mb-3'>
            <h5 className='mb-3'>Citizen's Charter (CC) Questions</h5>
            <table className='table table-hover table-striped-columns table-success'>
                <thead>
                    <tr>
                        <th></th>
                        <th>1</th>
                        <th>2</th>
                        <th>3</th>
                        <th>4</th>
                        <th>5</th>
                        <th>Total</th>
                    </tr>
                </thead>
                <tbody>
                    <Rows type='cc'/>
                </tbody>
            </table>
        </div>
    )
}