import { useContext, useEffect } from 'react'

import { GlobalState, GlobalStateDispatch } from '../contexts/globalContext'

import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'

import cswdo from '../assets/images/cswdo.png'

export default function Header({ refresh }) {
    const state = useContext(GlobalState)
    const dispatch = useContext(GlobalStateDispatch)

    const refresh_data = async () => {
        dispatch({ type: 'start-loading' })
        const data = await refresh()
        dispatch({ type: 'set-data', payload: data })
        dispatch({ type: 'filter-data' })
        dispatch({ type: 'end-loading' })
    }

    const setFilter = (e) => {
        if (e.target.value !== '') {
            if (e.target.name === 'month') {
                dispatch({ type: 'set-month', payload: e.target.value })
            } else if (e.target.name === 'year') {
                dispatch({ type: 'set-year', payload: e.target.value })
            }
        } else {
            alert('Filters cannot be empty.')
        }
    }

    useEffect(() => {
        dispatch({ type: 'filter-data' })
        //eslint-disable-next-line
    }, [state.month, state.year])

    return (
        <Navbar expand="md" className="bg-body-tertiary">
            <div className='container'>
                <Navbar.Brand onClick={refresh_data} style={{ cursor: 'pointer' }}>
                    <img alt='logo' src={cswdo} height={25} width={25} />
                    &nbsp;CSWDO-CSM&nbsp;<span style={{ fontSize: '0.80rem' }} className='text-secondary'>v.12.12.2023</span>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto gap-2 align-items-center">
                        <h6 className='mb-0'>Filter:</h6>
                        <select className='form-select' name='month' defaultValue={(new Date().getMonth() + 1).toString()} onChange={setFilter}>
                            <option value={'all'}>All</option>
                            {
                                [
                                    'January',
                                    'February',
                                    'March',
                                    'April',
                                    'May',
                                    'June',
                                    'July',
                                    'August',
                                    'September',
                                    'October',
                                    'November',
                                    'December'
                                ].map((month, index) => (
                                    <option key={index} value={(index + 1).toString()}>{month}</option>
                                ))
                            }
                        </select>
                        <input type='number' className='form-control' min={1990} max={new Date().getFullYear()} defaultValue={new Date().getFullYear()} name='year' onChange={setFilter} />
                        <button type='button' className='btn btn-success' onClick={refresh_data}>
                            <i className='bi bi-arrow-repeat' />
                        </button>
                    </Nav>
                </Navbar.Collapse>
            </div>
        </Navbar>
    )
}