import Spinner from 'react-bootstrap/Spinner'

export default function Loading() {
    return (
        <div className='row flex-grow-1 justify-content-center align-items-center text-center py-5 my-5'>
            <div className='col-6'>
                <div className='container bg-dark text-light py-5 rounded-4'>
                    <Spinner style={{height: '7rem', width: '7rem', borderWidth: '10px'}} className='mb-3' animation='grow' variant='info' role='status' />
                    <h2>Loading</h2>
                    <p>Please wait a moment...</p>
                </div>
            </div>
        </div>
    )
}