import { useContext } from 'react'
import { jsPDF } from 'jspdf'

import { GlobalState } from '../contexts/globalContext'

import CitizensCharter from './tables/CitizensCharter'
import SQD from './tables/SQD'
import Services from './tables/Services'

import city from '../assets/images/city.png'
import cswdo from '../assets/images/cswdo.png'

export default function Content() {
    const state = useContext(GlobalState)

    const months = {
        1: 'January',
        2: 'February',
        3: 'March',
        4: 'April',
        5: 'May',
        6: 'June',
        7: 'July',
        8: 'August',
        9: 'September',
        10: 'October',
        11: 'November',
        12: 'December',
        all: 'Year'
    }

    const export_tables_pdf = () => {
        let doc = new jsPDF()

        doc.html(document.querySelector('#tables'), {
            callback: function (doc) {
                for (let x = doc.internal.getNumberOfPages(); x > 1; x--) {
                    doc.deletePage(x)
                }

                doc.save('summary tables ' + new Date().toISOString() + '.pdf')
            },
            x: 10,
            y: 10,
            margin: [10, 10, 10, 10],
            autoPaging: 'text',
            width: 170,
            windowWidth: 910
        })
    }

    return (
        <div className='row flex-grow-1 justify-content-center py-5'>
            {state.count !== 0 ? (
                <>
                    <div id='tables' style={{ fontFamily: "'Poppins', sans-serif" }}>

                        <div className='row mb-3 justify-content-center gap-1 align-items-center text-center'>
                            <div className='col-2'>
                                <img src={city} className='header-logo' alt='city-logo' />
                            </div>
                            <div className='col-6'>
                                <h6>City of San Jose del Monte, Bulacan</h6>
                                <h5>City Social Welfare and Development Office</h5>
                            </div>
                            <div className='col-2'>
                                <img src={cswdo} className='header-logo' alt='cswdo-logo' />
                            </div>
                        </div>

                        <div className='container text-center mb-3'>
                            <h3>Client Satisfaction Feedback</h3>
                            <h5>{`${months[state.month]} ${state.year}`}</h5>
                        </div>

                        <CitizensCharter />
                        <SQD />
                        <Services />
                    </div>

                    <div className='container'>
                        <button type='button' className='btn btn-success' onClick={export_tables_pdf}>
                            Export tables as PDF
                        </button>
                    </div>
                </>
            ) : (
                <div className='container d-flex flex-grow-1 flex-column justify-content-center align-items-center text-center py-5'>
                    <div className='col-6'>
                        <i className='bi bi-question-diamond-fill text-info' style={{ fontSize: '10rem' }} />
                        <h2 className='fw-bold'>No data found</h2>
                        <p>No available data during this time <br />period. Please adjust time filters above <br />or refresh to fetch updated data online.</p>
                    </div>
                </div>
            )}
        </div>
    )
}