import Rows from './Rows'

export default function Services() {
    return (
        <div className='container py-3 mb-3'>
            <h5 className='mb-3'>Services Question</h5>
            <table className='table table-hover table-striped table-success'>
                <thead>
                    <tr>
                        <th>External Services</th>
                        <th>Overall Rating</th>
                    </tr>
                </thead>
                <tbody>
                    <Rows type='services' />
                </tbody>
            </table>
        </div>
    )
}